<template>
	<div class="login">
		<input type="text" placeholder="用户名"/>
		<br/>
		<input type="password" placeholder="密码"/>
		<br/>
		<button @click="skip(name,password)">登陆</button>
	</div>
	
</template>

<script>
	import {
		mapMutations
	}from 'vuex';
	export default{
		name:"login",
		data(){
			return{
				name:'sss',
				password:''
			}
		},
		methods:{	
			...mapMutations(['modifystate']),
			skip(_name,_password){
				this.$http.postlogin({name:_name,password:_password}).then(res=>{
//				console.log(res)
					if(res.data.res_code==1){
						//登陆成功修改登陆状态为true
						this.modifystate(true);
						window.localStorage.setItem("user",res.data.res_body.name);
						window.localStorage.setItem("token",res.data.res_body.token);
						console.log(this.$route);
						this.$router.push({name:this.$route.params.go.name})
					}
			})
			}
		}
	}
</script>

<style scoped lang="scss">
	.login{
		margin-top: 5vw;
		
		input{
			height: 10vw;
			margin: 5vw;
			border: none;
			border: 1px solid red;
			width: 80vw;
		}
		button{
			height: 10vw;
			width: 40vw;
			margin-left: 20vw;
		}
	}
</style>


